import React, { Component } from 'react'
import { Grid } from "@material-ui/core";

const scriptUrl = 'https://script.google.com/macros/s/AKfycbzihNMsNv0ENe24M9mitXiZurl2DOHXK1RMR5AX0v0lC9RP5WXhyLTNnxg7J6sTALNZ6A/exec'


class MappingsPane extends Component {
  constructor(props) {
    super(props)
    this.handleSelectAlgorithm = this.handleSelectAlgorithm.bind(this);
    this.handleSelectDataset = this.handleSelectDataset.bind(this);
  }

  state = {
    isInputFilled: false,
    isRadioSelected: false,
    buttonEnabled: false,
    exploredMaps: [],
    alleExplored: false,
    explorationMode: true,
    mapLabels: ['','',''],
    mapConfidences: ['','','']
  }

  componentDidMount() {
    // Add the first map to exploredMaps
    if (this.props.algorithm_options.length > 0) {
      this.setState({
        exploredMaps: [this.props.algorithm_options[0]]
      });
    }
    // set timestamp and message to log
    const timestamp = new Date().toLocaleString();
    const message = 'start exploration';
    this.exportLog({ target: { timestamp: timestamp, message: message, mapID: this.props.algorithm_options[this.props.algorithm_choice] } });
  }

  handleSelectAlgorithm(e) {
    let v = e.target.value;
    console.log("algorithm",v);
    this.props.selectAlgorithm(v)
  
    if (!this.state.exploredMaps.includes(v)) {
      this.setState(prevState => {
        let newExploredMaps = [...prevState.exploredMaps, v];
        let alleExplored = newExploredMaps.length === this.props.algorithm_options.length;
        return {
          exploredMaps: newExploredMaps,
          alleExplored: alleExplored
        };
      });
    }
    console.log("alleExplored",this.state.alleExplored);
    if(this.state.explorationMode){
      const timestamp = new Date().toLocaleString();
      const message = `switch map to ${v}`;
      this.exportLog({ target: { timestamp: timestamp, message: message, mapID: this.props.algorithm_options[this.props.algorithm_choice] } });
    }
  }

  
  componentDidUpdate(prevProps) {
    if (this.props.algorithm_choice !== prevProps.algorithm_choice) {
      this.setState(prevState => {
        let confidenceValue = prevState.mapConfidences[this.props.algorithm_choice];
        console.log("confidenceValue",confidenceValue);
        if (confidenceValue === '') {
          let confidenceRadios = document.querySelectorAll('input[name="confidence"]');
          confidenceRadios.forEach(radio => {
            radio.checked = false;
          });
        } else {
          let confidenceRadio = document.querySelector(`input[name="confidence"][value="${confidenceValue}"]`);
          if (confidenceRadio) {
            confidenceRadio.checked = true;
          }
        }
        return prevState;
      });
    }
  }

  handleSelectDataset(e) {
    let v = e.target.value;
    this.props.selectDataset(v);
  }

  handleInput(e) {
    let v = e.target.value;
    this.setState(prevState => {
      let newMapLabels = [...prevState.mapLabels, v];
      return {
        isInputFilled: v.length > 3,
        mapLabels: newMapLabels
      };
    });
    if (this.state.isInputFilled && this.state.isRadioSelected) {
      this.setState({ buttonEnabled: true });
    } else {
      this.setState({ buttonEnabled: false });
    }
  this.setState(prevState => {
    let newMapLabels = [...prevState.mapLabels];
    newMapLabels[e.target.map] = v;
    return {
      mapLabels: newMapLabels
    };
  });
  }

  handleRadio(e) {
    let v = e.target.value;
    this.setState(prevState => {
      let newMapConfidences = {...prevState.mapConfidences};
      newMapConfidences[this.props.algorithm_choice] = v;
      return {
        isRadioSelected: true,
        mapConfidences: newMapConfidences
      };
    });
    if (this.state.isInputFilled) {
      this.setState({ buttonEnabled: true });
    } else {
      this.setState({ buttonEnabled: false });
    }
  }

  handleNext() {
    if (this.props.algorithm_choice + 1 < this.props.algorithm_options.length) {
      this.handleSelectAlgorithm({ target: { value: this.props.algorithm_options[this.props.algorithm_choice + 1] } });
    } else {
      window.open(`https://tillmannohm.github.io/study-annotation/?id=${this.props.participantId}&dataset=${this.props.selectedDataset}`, '_self');
  }
  }

  handleFinish() {
    this.setState({ explorationMode: false });
    console.log("explorationMode",this.state.explorationMode);
    this.handleSelectAlgorithm({ target: { value: this.props.algorithm_options[0] } });
  }


  exportLog(e) {
    console.log("export log");
    const formData = new FormData();
    formData.append('sheetName', 'log');
    formData.append('timestamp', e.target.timestamp);
    formData.append('userID', this.props.participantId);
    formData.append('message', e.target.message);
    formData.append('mapID', e.target.mapID);

    fetch(scriptUrl, {
      method: 'POST', 
      body: formData,
    })
    .then(response => response.json())
    .then(data => {
      // nextAnnotation();
      if (data.result === 'success') {
        console.log('Data submitted successfully');
      } else {
        console.log('Failed to submit data');
        console.log(data);
      }
    })
    .catch(error => console.error('Error:', error));
  }
  



  exportLabels(e) {
    // setIsModalOpen(true); // Open the modal
    // render();
    const formData = new FormData();
    formData.append('sheetName', 'labels');
    formData.append('userID', this.props.participantId);
    formData.append('timestamp', Date.now());
    formData.append('mapID', e.target.map);
    formData.append('label', e.target.value);
    formData.append('confidence', e.target.confidence);

    fetch(scriptUrl, {
      method: 'POST', 
      body: formData,
    })
    .then(response => response.json())
    .then(data => {
      // nextAnnotation();
      if (data.result === 'success') {
        console.log('Data submitted successfully');
        
      } else {
        console.log('Failed to submit data');
        // // download the data as a JSON file
        // const file = new Blob([JSON.stringify(historyCopy)], { type: "application/json" });
        // const link = document.createElement('a');
        // link.href = URL.createObjectURL(file);
        // link.download = `canvas_export_${Date.now()}_${Math.floor(Math.random() * 3)}.json`;
        // link.click();
        // URL.revokeObjectURL(link.href);
        // // alert the user about the issue and the downloaded file
        // alert("There was an issue with the server. A download of your map was provided. Please send this file to the authors via email.");
      }
    })
    .catch(error => console.error('Error:', error));
    // setIsModalOpen(false); // Close the modal
  };




  render() {
    let {
      algorithm_options,
      algorithm_choice,
      selectedDataset,
    } = this.props;


    return (
      
      <Grid container spacing={1}>
          <Grid item xs={12}>
          <h1>2/4: Interactive Maps</h1>
          {this.state.explorationMode ? (
          <h3>Make yourself familiar with the collection and try to understand the ideas behind the different maps</h3>
          ) : (
            <h3>Please fill out the form for each map</h3>
          )}
          </Grid>
          <Grid item xs={12}>
          <h2>Map {algorithm_options[algorithm_choice]}</h2>
            <p>Switch between different maps:</p>
          </Grid>
          <div>
          <Grid item xs={12}>
            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
              {algorithm_options.map((option, index) => (
                <div key={option}>
                  <button 
                    type="button" 
                    style={{
                      backgroundColor: algorithm_options[algorithm_choice] === option ? '#808080' : '#4CAF50',
                      color: algorithm_options[algorithm_choice] === option ? 'darkgrey' : 'white',
                      cursor: algorithm_options[algorithm_choice] === option ? 'default' : 'pointer'
                    }}
                    className={algorithm_options[algorithm_choice] === option ? 'selected' : ''} 
                    onClick={() => this.handleSelectAlgorithm({ target: { value: option } })}
                  >
                    {option}
                  </button>
                </div>
              ))}
            </div>
          </Grid>
          </div>
          {!this.state.explorationMode ? (
                      <div>
                      <Grid item xs={12}>
                      <p>Give a meaningful label to describe the underlying similarity concept of this map:</p>
                      </Grid>
                      <Grid item xs={12}>
                      <input 
                        type="text"
                        className="full-width"
                        id="labelInput"
                        placeholder="Enter a descriptive label"
                        value={this.state.mapLabels[this.props.algorithm_choice] || ''}
                        onChange={(e) => {
                          this.handleInput({ target: { value: e.target.value, map: this.props.algorithm_choice } });
                        }}
                      />
                      </Grid>
                      {/* <Grid item xs={12}></Grid>
                      <Grid item xs={12}></Grid> */}
                      <Grid item xs={12}>
                      <p>I am confident about my answer and found the underlying similarity concept easy to describe.</p>
                      </Grid>
                      <Grid item xs={12}>
                      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <div style={{ width: '20%' }}>
                          <p style={{fontSize: 'small', transform: 'rotate(-45deg)'}}>Strongly<br/>Disagree</p>
                          <input type="radio" id="1" name="confidence" value="1" onChange={(e) => {this.handleRadio(e)}} />
                        </div>
                        <div style={{ width: '20%' }}>
                        <p style={{fontSize: 'small', transform: 'rotate(-45deg)'}}>&nbsp;<br/>Disagree</p>
                          <input type="radio" id="2" name="confidence" value="2" onChange={(e) => {this.handleRadio(e)}} />
                        </div>
                        <div style={{ width: '20%' }}>
                        <p style={{fontSize: 'small', transform: 'rotate(-45deg)'}}>&nbsp;<br/>Neutral</p>
                          <input type="radio" id="3" name="confidence" value="3" onChange={(e) => {this.handleRadio(e)}} />
                        </div>
                        <div style={{ width: '20%' }}>
                        <p style={{fontSize: 'small', transform: 'rotate(-45deg)'}}>&nbsp;<br/>Agree</p>
                          <input type="radio" id="4" name="confidence" value="4" onChange={(e) => {this.handleRadio(e)}} />
                        </div>
                        <div style={{ width: '20%' }}>
                        <p style={{fontSize: 'small', transform: 'rotate(-45deg)'}}>Strongly<br/>Agree</p>
                          <input type="radio" id="5" name="confidence" value="5" onChange={(e) => {this.handleRadio(e)}} />
                        </div>
                      </div>
                      </Grid>
                      <Grid item xs={12}>
                        <p></p>
                      </Grid>
                      
                      <Grid item xs={12}>
                        <button 
                          style={{
                            backgroundColor: this.state.buttonEnabled ? '#4CAF50' : '#808080',
                            color: this.state.buttonEnabled ? 'white' : 'darkgray',
                            cursor: this.state.buttonEnabled ? 'pointer' : 'not-allowed',
                          }}
                          onClick={() => {
                            const labelValue = document.getElementById('labelInput').value;
                            if(labelValue !== '') {
                              this.exportLabels({ target: { value: labelValue, map: algorithm_options[algorithm_choice], confidence: document.querySelector('input[name="confidence"]:checked').value } });
                              this.handleNext();
                            }
                          }}
                          disabled={!this.state.buttonEnabled}
                        >
                          submit
                        </button>
                      </Grid>
                      </div>
            ) : (
              <div>
                
                <Grid item xs={12}>
                  <p>Once you feel ready continue:</p>
                </Grid>
                <Grid item xs={12}>
                  <button 
                    style={{
                      backgroundColor: this.state.alleExplored ? '#4CAF50' : '#808080',
                      color: this.state.alleExplored ? 'white' : 'darkgray',
                      cursor: this.state.alleExplored ? 'pointer' : 'not-allowed',
                    }}
                    onClick={() => {
                      this.handleFinish();
                    }}
                  >
                    ready
                  </button>
                </Grid>
              </div>
          )}
        </Grid>
    )
  }
}

export default React.memo(MappingsPane)